import BasicDetailsForm from "./BasicDetailsForm";
import styles from "./index.module.scss";
import React, { useEffect, useState } from "react";
import GoBack from "components/UI/common/GoBack";
import Tabs from "./Tabs";
import StayDetailCreationForm from "./StayDetailCreationForm";
import RoomPackagesForm from "./RoomPackagesForm";
import ServiceForm from "./ServiceForm";
import ChargesForm from "./ChargesForm";
import PhotosForm from "./PhotosForm";
import CompanyDetailsForm from "./CompanyDetailsForm";
import PageLoader from "components/UI/common/PageLoader";
import { useLocation } from "react-router-dom";
import { history } from "helpers";
import { useDispatch, useSelector } from "react-redux";
import CommonModal from "components/UI/common/CommonModal";
import NormalButton from "components/UI/common/NormalButton/NormalButton";
import { useRef } from "react";
import { setTabDirty } from "redux/slice/basicSlice";
import { IconButton } from "@mui/material";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import debounce from "lodash/debounce";
import { getPropertyNotificationDetails } from "service/PropertyList";
import leftArrow from "assets/icons/leftArrowEnabled.svg";
import rightArrow from "assets/icons/rightArrowEnabled.svg";
import leftArrowDisabled from "assets/icons/leftArrowDisabled.svg";
import rightArrowDisabled from "assets/icons/rightArrowDisabled.svg";
import approvedTick from "assets/icons/approvedTick.svg";
import redInfo from "assets/icons/redInfo.svg";
import SearchLine from "assets/icons/clearSearchLine.svg";
import redLine from "assets/icons/redline.svg";
import greenLine from "assets/icons/greenline.svg";

const tabs = {
  0: BasicDetailsForm,
  1: StayDetailCreationForm,
  2: RoomPackagesForm,
  3: ServiceForm,
  4: ChargesForm,
  5: PhotosForm,
  6: CompanyDetailsForm,
};

const InventoryComp = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const propertyId = useSelector((state) => state.basic.propertyId);
  const PropertyName = useSelector((state) => state.basic.propertyName);
  const PropertyStatus = useSelector((state) => state.basic.propertyStatus);
  const TotalDraft = useSelector((state) => state.basic.TotalDraft);
  const PendingProperty = useSelector(
    (state) => state.basic.showPendingProperty
  );

  const searchParams = new URLSearchParams(location.search);
  const tab = searchParams.get("tab") || 0;
  const isPreview = searchParams.get("isPreview") ?? null;
  const tabDirty = useSelector((state) => state.basic.tabDirty);
  const [activeTab, setActiveTab] = useState(+tab || 0);
  const [loader, setLoader] = useState(false);
  const [loaderspin, setloaderspin] = useState(true);
  const [showPreview, setShowPreview] = useState(false);
  const [isWarn, setIsWarn] = useState(false);
  const [selectedRedirectIndex, setselectedRedirectIndex] = useState("");
  const [tabSwitch, setTabSwitch] = useState(false);
  const [notificationData, setNotificationData] = useState([]);
  const [LimitnotificationData, setLimitNotificationData] = useState({});
  const [tabLastNumber, setTabLastNumber] = useState(Number);
  const [tabCurrentNumber, setTabCurrentNumber] = useState(1);
  const [ViewReason, setViewReason] = useState(true);
  const Form = tabs[activeTab];
  const childSaveDraftRef = useRef(null);
  console.log(tabDirty, "gettheOutsideDirty");

  useEffect(() => {
    if (notificationData.length > 0) {
      // Ensure there is at least one item in the notificationData array
      setLimitNotificationData(notificationData[tabCurrentNumber - 1]);
    }
  }, [tabCurrentNumber, notificationData]);

  useEffect(() => {
    setLoader(true);
  }, [activeTab]);

  const getNotification = async () => {
    try {
      const response = await getPropertyNotificationDetails(propertyId);
      console.log(response?.data);
      if (response.status == 200) {
        setNotificationData(response?.data);
        if (response?.data?.length > 1) {
          setTabSwitch(true);
          setTabLastNumber(response?.data?.length);
        }
      }
    } catch (e) {
      console.log(e, "eee");
    }
  };

  useEffect(() => {
    setLoader(loaderspin);
  }, [loaderspin]);

  useEffect(() => {
    getNotification();
  }, []);
  useEffect(() => {
    if (typeof window !== "undefined") {
      setShowPreview(
        window.location.pathname.includes("/inventory/add-property/preview")
      );
    }
  }, [typeof window]);

  useEffect(() => {
    if (tab >= 0 && tab != activeTab) setActiveTab(+tab);
  }, [tab]);

  const handleChildSaveDraftClick = async () => {
    // Access and trigger a click event on the child component's button
    if (childSaveDraftRef?.current) {
      await childSaveDraftRef?.current?.click();
      setIsWarn(false);
    }
  };

  const handleNextTab = (
    newTab = null,
    isEdit,
    isPreviewScreen,
    isOverrideDirty
  ) => {
    console.log(tabDirty, "gettheDirty");
    console.log(isPreview, "isPreviewScreen");
    console.log(isPreviewScreen, "isPreview");
    if (newTab !== null) {
      if (
        tabDirty &&
        !isOverrideDirty &&
        ((isPreview && isPreviewScreen === undefined) ||
          (isPreview == null && isPreviewScreen == undefined))
      ) {
        if (newTab !== activeTab) {
          setIsWarn(true);
          setselectedRedirectIndex(newTab);
        }
        return;
      } else if (tabDirty && !isOverrideDirty) {
        setIsWarn(true);
        setselectedRedirectIndex(newTab);
        return;
      } else {
        if (activeTab < 6 || (newTab != null && newTab <= 6)) {
          let search = `?tab=${newTab ?? activeTab + 1}`;
          if (isEdit || isPreview) search += "&isPreview=true";
          history.push({
            pathname:
              (showPreview && !isEdit) || isPreviewScreen
                ? "/inventory/add-property/preview"
                : "/inventory/add-property",
            search,
          });
          dispatch(setTabDirty(false));
        }
        if (newTab === 6 && isEdit === true) {
          let search = `?tab=${newTab}`;
          if (isEdit || isPreview) search += "&isPreview=true";
          history.push({
            pathname:
              (showPreview && !isEdit) || isPreviewScreen
                ? "/inventory/add-property/preview"
                : "/inventory/add-property",
            search,
          });
          dispatch(setTabDirty(false));
        }
      }
    } else {
      if (activeTab < 6 || (newTab != null && newTab <= 6)) {
        let search = `?tab=${newTab ?? activeTab + 1}`;
        if (isEdit || isPreview) search += "&isPreview=true";
        history.push({
          pathname:
            (showPreview && !isEdit) || isPreviewScreen
              ? "/inventory/add-property/preview"
              : "/inventory/add-property",
          search,
        });
        dispatch(setTabDirty(false));
      }
      if (newTab === 6 && isEdit === true) {
        let search = `?tab=${newTab}`;
        if (isEdit || isPreview) search += "&isPreview=true";
        history.push({
          pathname:
            (showPreview && !isEdit) || isPreviewScreen
              ? "/inventory/add-property/preview"
              : "/inventory/add-property",
          search,
        });
        dispatch(setTabDirty(false));
      }
    }
  };

  const handleDontSaveSupport = (newTab = null, isEdit, isPreviewScreen) => {
    if (activeTab < 6 || (newTab != null && newTab <= 6)) {
      let search = `?tab=${newTab ?? activeTab + 1}`;
      if (isEdit || isPreview) search += "&isPreview=true";
      history.push({
        pathname:
          (showPreview && !isEdit) || isPreviewScreen
            ? "/inventory/add-property/preview"
            : "/inventory/add-property",
        search,
      });
      dispatch(setTabDirty(false));
    }
    if (newTab === 6 && isEdit === true) {
      let search = `?tab=${newTab}`;
      if (isEdit || isPreview) search += "&isPreview=true";
      history.push({
        pathname:
          (showPreview && !isEdit) || isPreviewScreen
            ? "/inventory/add-property/preview"
            : "/inventory/add-property",
        search,
      });
      dispatch(setTabDirty(false));
    }
  };

  const handleDontSaveClick = async () => {
    await dispatch(setTabDirty(false));
    await setIsWarn(false);
    isPreview
      ? handleDontSaveSupport(selectedRedirectIndex, false, true)
      : handleDontSaveSupport(selectedRedirectIndex);
  };

  console.log(PropertyStatus, "setpropstatus");

  console.log(PropertyName, "setpropname");
  console.log(TotalDraft, "settotaldraft");
  console.log(showPreview, "setshowpreview");
  console.log(PendingProperty, "PendingProperty");

  return (
    <div style={{ overflow: "auto" }}>
      {PropertyStatus == "REJECTED" && notificationData.length > 0 &&
        <div
          className={
            LimitnotificationData?.approvalStatus == "REJECTED"
              ? styles.RejectedNotification
              : styles.ApprovedNotification
          }
        >
          {tabSwitch && (
            <div className={styles.switchBtn}>
              <img
                src={1 === tabCurrentNumber ? leftArrowDisabled : leftArrow}
                onClick={() =>
                  1 !== tabCurrentNumber
                    ? setTabCurrentNumber(tabCurrentNumber - 1)
                    : null
                }
              />
              <span className={styles.switchvalue}>
                {tabCurrentNumber}/{tabLastNumber}
              </span>
              <img
                src={
                  tabLastNumber === tabCurrentNumber
                    ? rightArrowDisabled
                    : rightArrow
                }
                onClick={() =>
                  tabLastNumber !== tabCurrentNumber
                    ? setTabCurrentNumber(tabCurrentNumber + 1)
                    : null
                }
              />
            </div>
          )}
          <div className="w-100 ms-4 d-flex justify-content-between my-3">
            <div>
              <img
                src={
                  LimitnotificationData?.approvalStatus == "REJECTED"
                    ? redInfo
                    : approvedTick
                }
              />
              <span
                className={
                  LimitnotificationData?.approvalStatus == "REJECTED"
                    ? styles.rejectedheader
                    : styles.approvedheader
                }
              >
                {LimitnotificationData?.notificationText}
                {ViewReason && (
                  <p className={styles.viewReason}>
                    {" "}
                    {LimitnotificationData?.message}
                  </p>
                )}
              </span>
            </div>
          </div>
        </div>
      }

      <div className="cursor-pointer">
        <GoBack
          pageName={
            (!TotalDraft && PropertyStatus !== "APPROVED" && PropertyStatus !== "REJECTED" && PropertyName !== "") ||
            (PropertyStatus == "APPROVED" || PropertyStatus == "REJECTED" && PropertyName !== "") ||
              (!PendingProperty && showPreview)
              ? PropertyName
              : showPreview &&
                Boolean(TotalDraft) &&
                PropertyStatus == "PENDING"
                ? "Property Preview"
                : "Add Property"
          }
          Status={PropertyStatus === "APPROVED" || PropertyStatus === "REJECTED" || (PropertyStatus === "PENDING" && !TotalDraft) ? true : false}
          approved={PropertyStatus === "APPROVED" ? true : false}
          pending={PropertyStatus === "PENDING" ? true : false}
          statusValue={PropertyStatus}
        />
      </div>
      <span className={`${styles.breadcrumbs}`}>Property List / </span>
      {/* {showPreview && !TotalDraft && (
        <span
          className={
            PropertyStatus !== "" ? styles.labelColor : styles.breadcrumbs
          }
        >
          {PropertyStatus !== "" ? PropertyName : "Add Property /"}
        </span>
      )}
      {PropertyStatus == "PENDING" && TotalDraft && (
        <span className={`${styles.labelColor}`}>
          {showPreview ? "Property Preview" : "Add Property"}
        </span>
      )}
      {!TotalDraft && (
        <span className={`${styles.labelColor}`}>
          {showPreview ? "Add Property" : PropertyName}
        </span>
      )} */}

      <span className={`${styles.labelColor}`}>
        
        {
          (!TotalDraft && PropertyStatus !== "APPROVED" && PropertyStatus !== "REJECTED" && PropertyName !== "") ||
          (PropertyStatus == "APPROVED" || PropertyStatus == "REJECTED" && PropertyName !== "") ||
          (!PendingProperty && showPreview)
          ? PropertyName
          : showPreview && TotalDraft && PropertyStatus == "PENDING"
            ? "Property Preview"
            : "Add Property"}
      </span>

      <div className={styles.card}>
        <Tabs
          loaderspin={loaderspin}
          setloaderspin={setloaderspin}
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          handleActiveTab={(data) => handleNextTab(data)}
          showPreview={showPreview}
          tabDirty={tabDirty}
          isPreviewScreen={isPreview}
        />
        {loader ? (
          <PageLoader />
        ) : (
          <Form
            handleNextTab={handleNextTab}
            showPreview={showPreview}
            activeTab={activeTab}
            isPreviewScreen={isPreview}
            childSaveDraftRef={childSaveDraftRef}
          />
        )}
      </div>
      <CommonModal visible={isWarn} handleClose={() => setIsWarn(false)}>
        <div className="px-2">
          <div className="d-flex justify-content-end">
            <IconButton
              aria-label="close"
              sx={{
                position: "absolute",
                right: 8,
                top: 8,
                borderRadius: "16px",
                color: (theme) => theme.palette.grey[500],
              }}
              style={{ borderRadius: 0 }}
              onClick={() => setIsWarn(false)}
            >
              <HighlightOffIcon style={{ color: "#090909" }} />
            </IconButton>
          </div>
          <div
            style={{ color: "#1E2528" }}
            className="py-2"
          >{`You are trying to navigate to a different page in the middle of adding property.`}</div>
          {isPreview ? (
            <div
              style={{ color: "#1E2528" }}
              className="pt-3"
            >{`Your unsaved changes would be lost!`}</div>
          ) : (
            <div className="pt-3">{`Please choose one option from below.`}</div>
          )}
          <div className="d-flex w-100 col-12 justify-content-center my-2 mt-5">
            <div className="col-6 px-2 ">
              <NormalButton
                className={"cancelBtn w-100 "}
                label={isPreview ? "Continue" : "Don't save"}
                onClick={async () => handleDontSaveClick()}
              />
            </div>
            <div className="col-6 px-2">
              <NormalButton
                onClick={debounce(() => {
                  if (isPreview) {
                    setIsWarn(false);
                  } else {
                    handleChildSaveDraftClick();
                  }
                }, 800)}
                className={"submitBtn w-100 "}
                label={isPreview ? "Cancel" : "Save as draft"}
              />
            </div>
          </div>
        </div>
      </CommonModal>
    </div>
  );
};

export default InventoryComp;
