import React from "react";
import styles from "./index.module.scss";
import { ReactComponent as Back } from "assets/icons/back.svg";
import { history } from "helpers";
const GoBack = ({
  pageName,
  handleBack,
  Status = false,
  statusValue,
  approved = false,
  pending = false,
}) => {
  return (
    <div
      className={`d-flex justify-content-start align-items-center  ${styles.goback}`}
    >
      <Back
        onClick={() => history.push("/inventory")}
        className="cursor-pointer"
        width={30}
        height={30}
      />
      <span className={styles.pageName}>{pageName}</span>

      {Status && (
        <div
          className={
            approved
              ? styles.ApprovedPropertyStatus
              : pending
              ? styles.PendingPropertyStatus
              : styles.RejectedPropertyStatus
          }
        >
          <span
            className={
              approved
                ? styles.ApprovedProperty
                : pending
                ? styles.PendingProperty
                : styles.RejectedProperty
            }
          >
            {statusValue == "REJECTED"
              ? "Rejected"
              : statusValue == "PENDING"
              ? "Pending"
              : "Approved"}
          </span>
        </div>
      )}
    </div>
  );
};

export default GoBack;
