import React, { useState, useRef, useEffect, useCallback } from "react";
import styles from "./index.module.scss";
import searchIcon from "assets/icons/Inventory/searchIcon.svg";
import Pagination from "./Pagination";
import NormalButton from "components/UI/common/NormalButton/NormalButton";
import { debounceFunction, history } from "helpers";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import {
  deActivatePropertyList,
  deletePropertyList,
  getPropertyListDetails,
  getPropertyNotificationDetails,
  readPropertyNotificationDetails,
} from "service/PropertyList";
import moment from "moment";
import info from "assets/icons/infoIcon.svg";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import {
  BsArrowCounterclockwise,
  BsArrowLeft,
  BsArrowRight,
  BsDashCircle,
  BsFileMinus,
  BsPlus,
  BsPlusCircle,
  BsPlusCircleFill,
  BsTrash,
} from "react-icons/bs";
import Comment from "assets/icons/commentIcon.svg";
import CommentLight from "assets/icons/commentIconLight.svg";

import { Page } from "react-pdf";
import { useDispatch, useSelector } from "react-redux";
import {
  setBasicDetails,
  setPropertyId,
  setPropertyName,
  setPropertyStatus,
  setShowPendingProperty,
  setTotalDraft,
  setUnitCreationDraft,
} from "redux/slice/basicSlice";
import PropertyLogo from "assets/icons/noPropertyLogo.svg";
import DeleteModal from "components/UI/common/DeleteModal";
import PageLoader from "components/UI/common/PageLoader";
import AddBtnLogo from "assets/icons/addBtn.svg";
import { ContactlessOutlined, PlusOneOutlined } from "@mui/icons-material";
import { getTabStatusDetails } from "service/TabDetails";
import { AiOutlineConsoleSql } from "react-icons/ai";
import redInfo from "assets/icons/redInfo.svg";
import redLine from "assets/icons/redline.svg";
import greenLine from "assets/icons/greenline.svg";
import leftArrow from "assets/icons/leftArrowEnabled.svg";
import rightArrow from "assets/icons/rightArrowEnabled.svg";
import leftArrowDisabled from "assets/icons/leftArrowDisabled.svg";
import rightArrowDisabled from "assets/icons/rightArrowDisabled.svg";
import approvedTick from "assets/icons/approvedTick.svg";
import SearchLine from "assets/icons/clearSearchLine.svg";
import { useForm } from "react-hook-form";
import CommonLabelInput from "components/UI/common/CommonLabelInput";
const PropertyListComp = () => {
  const dispatch = useDispatch();
  const propertyTypes = useSelector((state) => state.basic.propertyTypes);
  const [isFrameOpen, setIsFrameOpen] = useState(false);
  const [activeFrameIndex, setActiveFrameIndex] = useState(null);
  const [tableData, setTableData] = useState([]);
  const [showStatusDropdown, setShowStatusDropdown] = useState(false);
  const [showPropertyDropdown, setShowPropertyDropdown] = useState(false);
  const statusDropdownRef = useRef(null);
  const [isSearchBarOpen, setIsSearchBarOpen] = useState(false);
  const [isSearchBarOpenCity, setIsSearchBarOpenCity] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [statusValue, setStatus] = useState(null);
  const [propertyType, setPropertyType] = useState("");
  const [searchSecondaryPOC, setsearchSecondaryPOC] = useState("");
  const [searchPrimaryPOC, setsearchPrimaryPOC] = useState("");
  const [searchCity, setsearchCity] = useState("");
  const [searchArea, setsearchArea] = useState("");
  const [searchName, setsearchName] = useState("");
  const [deleteModal, setDeleteModal] = useState(false);
  const [reasonModal, setReasonModal] = useState(false);
  const [empty, setEmpty] = useState(false);
  const [deleteValue, setDeleteValue] = useState("");
  const [loader, setloader] = useState(false);
  const [totalPages, settotalPages] = useState(1);
  const [cursorLoading, setCursorLoading] = useState(false);
  const [tabSwitch, setTabSwitch] = useState(false);
  const [notificationData, setNotificationData] = useState([]);
  const [LimitnotificationData, setLimitNotificationData] = useState({});
  const [tabLastNumber, setTabLastNumber] = useState(Number);
  const [tabCurrentNumber, setTabCurrentNumber] = useState(1);
  const [indexValue, setindexValue] = useState(0);
  const [indexData, setindexData] = useState({});
  const [statusReason, setStatusReason] = useState("");

  const [ViewReason, setViewReason] = useState(false);

  const statusDropdownData = [
    { label: "All", value: "" },
    { label: "Pending", value: "PENDING" },
    { label: "Approved", value: "APPROVED" },
    { label: "Rejected", value: "REJECTED" },
  ];
  const {
    register,
    handleSubmit,
    getValues,
    setValue,
    reset,
    control,
    formState: { errors, isDirty },
  } = useForm({
    //mode: "all",
    mode: "onChange",
  });

  useEffect(() => {
    if (notificationData.length > 0) {
      // Ensure there is at least one item in the notificationData array
      setLimitNotificationData(notificationData[tabCurrentNumber - 1]);
    }
  }, [tabCurrentNumber, notificationData]);

  useEffect(() => {
    setViewReason(false);
  }, [tabCurrentNumber]);

  const LightTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: theme.palette.common.white,
      color: "#000",
      boxShadow: theme.shadows[5],
      fontSize: 11,
      width: "900px",
      margin: "20px -15px 0 0px",
      borderRadius: "10px",
    },

    [`& .${tooltipClasses.arrow}`]: {
      color: "#FFFFFF",
      margin: "0 0px 0 -7px",
    },
  }));

  const handleSearchSecondary = useCallback(
    debounceFunction((value) => {
      setsearchSecondaryPOC(value);
    }, 500),
    []
  );
  const handleSearchPrimary = useCallback(
    debounceFunction((value) => {
      setsearchPrimaryPOC(value);
      setCurrentPage(1);
    }, 500),
    []
  );
  const handleSearchCity = useCallback(
    debounceFunction((value) => {
      setsearchCity(value);
      setCurrentPage(1);
    }, 500),
    []
  );
  const handleSearchArea = useCallback(
    debounceFunction((value) => {
      setsearchArea(value);
      setCurrentPage(1);
    }, 500),
    []
  );
  const handleSearchName = useCallback(
    debounceFunction((value) => {
      setsearchName(value);
      setCurrentPage(1);
    }, 500),
    []
  );

  const getNotification = async () => {
    try {
      const response = await getPropertyNotificationDetails();
      console.log(response?.data);
      if (response.status == 200) {
        setNotificationData(response?.data);
        if (response?.data?.length > 1) {
          setTabSwitch(true);
          setTabLastNumber(response?.data?.length);
        }
      }
    } catch (e) {
      console.log(e, "eee");
    }
  };

  const getPropertyDetails = async () => {
    try {
      setloader(true);
      const approvalStatus = statusValue;
      const type = propertyType;
      const primaryPOC = searchPrimaryPOC;
      const secondaryPOC = searchSecondaryPOC;
      const area = searchArea;
      const city = searchCity;
      const page = currentPage;
      const name = searchName;
      const limit = 10;

      const data = await getPropertyListDetails(
        approvalStatus,
        type,
        primaryPOC,
        secondaryPOC,
        area,
        city,
        page,
        name,
        limit
      );
      setTableData(data?.data?.data?.list);
      setEmpty(data?.data?.data.isEmpty);
      settotalPages(data?.data?.data?.pageMeta?.totalPages);
      await setloader(false);
    } catch (e) {
      console.log(e, "e :==>");
      setloader(false);
    }
  };

  useEffect(() => {
    getNotification();
    getPropertyDetails();
  }, [
    statusValue,
    propertyType,
    searchSecondaryPOC,
    searchPrimaryPOC,
    searchCity,
    searchArea,
    searchName,
    currentPage,
  ]);
  const handleAddProperty = async () => {
    let defaultValue = {
      propertyName: "",
      propertyType: "",
      propertyTypeName: "",
      status: "",
      landlineStateCode: "",
      landlineNumber: "",
      countryCode: "+91",
      mobileNumber: "",
      residentType: [],
      website: "",
      locationDetails: {
        addressLine1: "",
        addressLine2: "",
        country: "",
        state: "",
        city: "",
        area: "",
        landmark: "",
        postalCode: "",
        googleMapLink: "",
        latitude: "",
        longitude: "",
        stateISO: "",
        countryISO: "",
      },
      pocDetails: {
        name: "",
        emailId: "",
        countryCode: "+91",
        mobileNumber: "",
        role: "",
        staffId: "64ec6e75f928701f01dd1c84",
      },
      secPocDetails: [
        {
          name: "",
          emailId: "",
          countryCode: "+91",
          mobileNumber: "",
          role: "",
          staffId: "64ec6e75f928701f01dd1c84",
        },
        {
          name: "",
          emailId: "",
          countryCode: "+91",
          mobileNumber: "",
          role: "",
          staffId: "64ec6e75f928701f01dd1c84",
        },
      ],
    };
    await dispatch(setBasicDetails(defaultValue));
    await dispatch(setPropertyId(""));
    await dispatch(setPropertyName(""));
    await dispatch(setPropertyStatus(""));
    await dispatch(setTotalDraft(false));
    await dispatch(setShowPendingProperty(false));

    history.push("/inventory/add-property");
  };

  const handlePreviewPage = (data) => {
    console.log(data);
    try {
      setCursorLoading(true);
      dispatch(setPropertyName(data?.propertyName));
      dispatch(setPropertyStatus(data?.approvalStatus));
      dispatch(setTotalDraft(data?.isTotalDraft));
      dispatch(setUnitCreationDraft(null));
      dispatch(setPropertyId(data._id));
      if (data?.isTotalDraft == true) {
        dispatch(setShowPendingProperty(true));
      } else if (data?.isTotalDraft == false) {
        dispatch(setShowPendingProperty(false));
      }
      if (data?.isTotalDraft == true) {
        getTabStatusDetails(data._id)
          .then((resp) => {
            if (resp?.data?.length) {
              const getPendingTab = resp?.data?.findIndex((a) =>
                ["IN PROGRESS", "UP NEXT"].includes(a?.tabStatus)
              );
              if (getPendingTab >= 0)
                history.push("/inventory/add-property?tab=" + getPendingTab);
              else history.push("/inventory/add-property");
            } else history.push("/inventory/add-property");
            setCursorLoading(false);
          })
          .catch((e) => {
            history.push("/inventory/add-property");
            console.log("error in getting tab status", e);
            setCursorLoading(false);
          });
      } else history.push("/inventory/add-property/preview");
    } catch (error) {
      console.log("error in preview handler", error);
    } finally {
      setTimeout(() => setCursorLoading(false), 1000);
    }
  };
  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  // const toggleSearchBar = () => {
  //   setIsSearchBarOpen((prevState) => !prevState);
  // };
  const toggleSearchBarCity = () => {
    setIsSearchBarOpenCity((prevState) => !prevState);
  };
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        statusDropdownRef.current &&
        !statusDropdownRef.current.contains(event.target)
      ) {
        setIsFrameOpen((open) => !open);
        setActiveFrameIndex(null);
      }
    };

    window.addEventListener("click", handleClickOutside, true);
    return () => {
      // eslint-disable-next-line no-restricted-globals
      window.removeEventListener("click", removeEventListener, true);
    };
  }, []);

  const handleOpenFrame = (row, index) => {
    setindexValue(index);
    setindexData(row);
    if (activeFrameIndex === index) {
      setActiveFrameIndex(null);
      setIsFrameOpen(false);
    } else {
      setActiveFrameIndex(index);
      setIsFrameOpen(true);
    }
  };

  const handleDelete = async (id) => {
    try {
      setloader(true);
      const response = await deletePropertyList(deleteValue);
      if (response.status == 200) {
        setActiveFrameIndex(null);
        getPropertyDetails();
        setDeleteModal(false);
        setDeleteValue("");
        setloader(false);
      }
    } catch (e) {
      setloader(false);
      console.log("e=>", e);
    }
  };

  const handleDeleteFunction = (id) => {
    setDeleteModal(true);
    setDeleteValue(id);
  };

  const handleDeActivateAccount = async (id) => {
    try {
      let body = {
        id,
        status: "DEACTIVATED",
      };
      setloader(true);
      const response = await deActivatePropertyList(body);
      if (response.status == 200) {
        setActiveFrameIndex(null);
        getPropertyDetails();
        setDeleteModal(false);
        setDeleteValue("");
        setloader(false);
      }
    } catch (e) {
      console.log(e, "eee=>");
    }
  };

  const handleReActivateAccount = async (id) => {
    try {
      let body = {
        id,
        status: "ACTIVE",
      };
      setloader(true);
      const response = await deActivatePropertyList(body);
      if (response.status == 200) {
        setActiveFrameIndex(null);
        getPropertyDetails();
        setDeleteModal(false);
        setDeleteValue("");
        setloader(false);
      }
    } catch (e) {
      console.log(e, "eee=>");
    }
  };

  console.log(LimitnotificationData);
  const handleClearSearch = () => {
    setsearchArea("");
    setsearchName("");
    setsearchCity("");
    setsearchSecondaryPOC("");
    setsearchPrimaryPOC("");
    setValue("searchArea", "");
    setValue("searchprimaryPoc", "");
    setValue("searchSecondaryPoc", "");
    setValue("searchCity", "");
    setValue("searchName", "");
  };
  const handleViewReason = (id) => {
    setViewReason(!ViewReason);
    let body = {
      id,
      isAddressed: true,
    };
    const updateStatus = readPropertyNotificationDetails(body);
    console.log(updateStatus);
  };

  console.log(LimitnotificationData);
  return (
    <>
      {/* <div className={styles.property}>Property List</div> */}
      {notificationData.length > 0 && (
        <div
          className={
            LimitnotificationData?.approvalStatus == "REJECTED"
              ? styles.RejectedNotification
              : styles.ApprovedNotification
          }
        >
          {tabSwitch && (
            <div className={styles.switchBtn}>
              <img
                src={1 === tabCurrentNumber ? leftArrowDisabled : leftArrow}
                onClick={() =>
                  1 !== tabCurrentNumber
                    ? setTabCurrentNumber(tabCurrentNumber - 1)
                    : null
                }
              />
              <span className={styles.switchvalue}>
                {tabCurrentNumber}/{tabLastNumber}
              </span>
              <img
                src={
                  tabLastNumber === tabCurrentNumber
                    ? rightArrowDisabled
                    : rightArrow
                }
                onClick={() =>
                  tabLastNumber !== tabCurrentNumber
                    ? setTabCurrentNumber(tabCurrentNumber + 1)
                    : null
                }
              />
            </div>
          )}
          <div className="w-100 ms-4 d-flex justify-content-between my-3">
            <div>
              <img
                src={
                  LimitnotificationData?.approvalStatus == "REJECTED"
                    ? redInfo
                    : approvedTick
                }
              />
              <span
                className={
                  LimitnotificationData?.approvalStatus == "REJECTED"
                    ? styles.rejectedheader
                    : styles.approvedheader
                }
              >
                {LimitnotificationData?.notificationText}
                {ViewReason && (
                  <p className={styles.viewReason}>
                    {" "}
                    {LimitnotificationData?.message}
                  </p>
                )}
              </span>
            </div>
            <div>
              <img
                src={
                  LimitnotificationData?.approvalStatus == "REJECTED"
                    ? redLine
                    : greenLine
                }
              />
              <button
                onClick={() =>
                  LimitnotificationData?.approvalStatus == "REJECTED"
                    ? handleViewReason(LimitnotificationData?.id)
                    : null
                }
                className={styles.ViewResonbtn}
              >
                {LimitnotificationData?.approvalStatus == "REJECTED"
                  ? ViewReason
                    ? "Hide Reason"
                    : "View Reason"
                  : "Add Units"}
              </button>
            </div>
          </div>
        </div>
      )}
      <div className={styles.frame}>
        <div className={styles.group}>
          <div className={styles.divWrapper}>
            <div className={styles.propertyHeaderLabel}>Property List</div>
          </div>
        </div>
        {tableData?.length >= 0 && empty == false && (
          <NormalButton
            addBtn={true}
            onClick={() => handleAddProperty()}
            className={"addNewBtn"}
            label="Add Property"
          />
        )}
      </div>
      <div className={styles.card}>
        {console.log(empty)}
        {tableData && empty === false ? (
          <>
            <div className={styles.frame}>
              <div className={styles.approvalStatusBox}>
                <p className={styles.approvalStatusLabel}>Approval Status:</p>
                <div className={styles.approvalStatusList}>
                  <div className={styles.div2}>
                    <div className={styles.ellipse}></div>
                    <div className={styles.pending}>Pending</div>
                  </div>
                  <div className={styles.div2}>
                    <div className={styles.ellipse2}></div>
                    <div className={styles.approved}>Approved</div>
                  </div>
                  <div className={styles.div2}>
                    <div className={styles.ellipse3}></div>
                    <div className={styles.rejected}>Rejected</div>
                  </div>
                </div>
              </div>

              {/* Status Dropdown */}
              <div className="col-6 row">
                <div className="col-3 d-flex align-items-center justify-content-end">
                  <span
                    onClick={() => {
                      if (
                        searchName !== "" ||
                        searchCity !== "" ||
                        searchArea !== "" ||
                        searchPrimaryPOC !== "" ||
                        searchSecondaryPOC !== ""
                      ) {
                        handleClearSearch();
                      }
                    }}
                    className={
                      searchName !== "" ||
                      searchCity !== "" ||
                      searchArea !== "" ||
                      searchPrimaryPOC !== "" ||
                      searchSecondaryPOC !== ""
                        ? styles.ClearSearchDark
                        : styles.ClearSearch
                    }
                  >
                    Clear Search
                  </span>
                  <img src={SearchLine} />
                </div>
                <div className="col-4">
                  <NormalMultiSelect
                    name="status"
                    isSearchable={false}
                    options={statusDropdownData}
                    placeholder={"Status: All"}
                    handleChange={(e) => {
                      setCurrentPage(1);
                      setStatus(e.target.value);
                    }}
                    value={statusValue == "" ? statusValue : null}
                    supportLabel="Status: "
                  />
                </div>
                <div className="col-5">
                  <NormalMultiSelect
                    name="status"
                    isSearchable={false}
                    options={[
                      {
                        value: null,
                        label: "All",
                      },
                      ...propertyTypes,
                    ]}
                    placeholder={"Property type: All"}
                    handleChange={(e) => {
                      setCurrentPage(1);
                      setPropertyType(e.target.value);
                    }}
                    value={propertyType}
                    supportLabel="Property type: "
                  />
                </div>
              </div>
            </div>

            <div className={styles.container}>
              <div className={styles.tableContainer}>
                <table className={styles.table}>
                  <thead>
                    <tr>
                      <th style={{ padding: "14px 12px 14px 30px" }}>ID</th>
                      <th>
                        <div className={styles.columnContainerName}>
                          <div className={styles.propertyHeaderNameBorder}>
                            Property Name
                          </div>
                          <img
                            className={styles.searchIconCity}
                            src={searchIcon}
                            alt="Search"
                            onClick={toggleSearchBarCity}
                          />
                        </div>
                      </th>

                      <th>Type</th>
                      <th>Property Status</th>
                      <th>Created On</th>
                      <th>
                        <div className={styles.columnContainer1}>
                          <div className={styles.propertyName}>City</div>
                          <img
                            className={styles.searchIconCity}
                            src={searchIcon}
                            alt="Search"
                            onClick={toggleSearchBarCity}
                          />
                        </div>
                      </th>
                      <th>
                        <div className={styles.columnContainer1}>
                          <div className={styles.propertyName}>Area</div>
                          <img
                            className={styles.searchIconCity}
                            src={searchIcon}
                            alt="Search"
                            onClick={toggleSearchBarCity}
                          />
                        </div>
                      </th>
                      <th style={{ width: "140px" }}>Property Contact</th>
                      <th>
                        <div className={styles.columnContainerPrimary}>
                          <div className={styles.propertyName}>Primary POC</div>
                          <img
                            className={styles.searchIcon}
                            src={searchIcon}
                            alt="Search"
                            onClick={toggleSearchBarCity}
                          />
                        </div>
                      </th>
                      <th style={{ width: "160px" }}>
                        <div className={styles.columnContainer}>
                          <div className={styles.propertyName}>
                            Secondary POC
                          </div>
                          <img
                            className={styles.searchIcon}
                            src={searchIcon}
                            alt="Search"
                            onClick={toggleSearchBarCity}
                          />
                        </div>
                      </th>
                      <th style={{ width: "120px" }}>Total Units</th>
                      <th className={styles.lastColumnLeftBorder}></th>
                      {/* <th className={styles.lastColumnBorder}></th> */}
                    </tr>
                  </thead>
                  <tbody className={styles.tablebody}>
                    {isSearchBarOpen || isSearchBarOpenCity ? (
                      <tr className={styles.searchRow}>
                        <td></td>
                        <td
                          className={styles.propertyHeaderNameBorder}
                          style={{ zIndex: 1 }}
                        >
                          <div
                            style={{
                              marginTop: "-14px",
                              zIndex: 5,
                              paddingRight: "10px",
                            }}
                            className={styles.searchContainer}
                          >
                            <CommonLabelInput
                              className={styles.searchInput}
                              name="searchName"
                              placeholder="Search..."
                              type="text"
                              value={searchName}
                              onChange={(e) => handleSearchName(e.target.value)}
                              register={register("searchName", {
                                required: false,
                              })}
                            />
                          </div>
                        </td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td>
                          <div
                            className={styles.searchContainer}
                            style={{
                              marginTop: "-14px",
                              paddingRight: "40px",
                              zIndex: 0,
                            }}
                          >
                            <CommonLabelInput
                              className={styles.searchInput}
                              name="searchCity"
                              placeholder="Search..."
                              type="text"
                              value={searchCity}
                              onChange={(e) => handleSearchCity(e.target.value)}
                              register={register("searchCity", {
                                required: false,
                              })}
                            />
                          </div>
                        </td>
                        {/* <td></td> */}
                        <td>
                          <div
                            className={styles.searchContainer}
                            style={{ marginTop: "-14px", paddingRight: "40px" }}
                          >
                            <CommonLabelInput
                              className={styles.searchInput}
                              name="searchArea"
                              placeholder="Search..."
                              type="text"
                              value={searchArea}
                              onChange={(e) => handleSearchArea(e.target.value)}
                              register={register("searchArea", {
                                required: false,
                              })}
                            />
                          </div>
                        </td>
                        <td></td>
                        <td>
                          <div
                            className={styles.searchContainer}
                            style={{ marginTop: "-14px" }}
                          >
                            <CommonLabelInput
                              className={styles.searchInput}
                              name="searchprimaryPoc"
                              placeholder="Search..."
                              type="text"
                              value={searchPrimaryPOC}
                              onChange={(e) =>
                                handleSearchPrimary(e.target.value)
                              }
                              register={register("searchprimaryPoc", {
                                required: false,
                              })}
                            />
                          </div>
                        </td>
                        <td>
                          <div
                            className={styles.searchContainer}
                            style={{ marginTop: "-14px" }}
                          >
                            <CommonLabelInput
                              className={styles.searchInput}
                              name="searchSecondaryPoc"
                              placeholder="Search..."
                              type="text"
                              value={searchSecondaryPOC}
                              onChange={(e) =>
                                handleSearchSecondary(e.target.value)
                              }
                              register={register("searchSecondaryPoc", {
                                required: false,
                              })}
                            />
                          </div>
                        </td>
                        <td></td>
                        <td className={styles.lastColumnLeftBorder}></td>
                      </tr>
                    ) : null}

                    {tableData && tableData?.length && !loader > 0 ? (
                      tableData.map((row, index) => (
                        <tr key={index}>
                          <td className=" ps-0">
                            <div
                              className={`${styles.circle} d-flex justify-content-around `}
                            >
                              {row?.approvalStatus == "PENDING" ? (
                                row?.isTotalDraft == false ? (
                                  <div className={styles.ellipse}></div>
                                ) : (
                                  <div>&nbsp;</div>
                                )
                              ) : row?.approvalStatus == "APPROVED" ? (
                                <div className={styles.ellipse2}></div>
                              ) : row?.approvalStatus == "REJECTED" ? (
                                <div className={styles.ellipse3}></div>
                              ) : (
                                <div>&nbsp;</div>
                              )}
                              {row.propertyNumber ? row.propertyNumber : "-"}
                            </div>
                          </td>

                          <td
                            className={`${styles.propertyNameBorder}  cursor-pointer`}
                          >
                            <div className="d-flex justify-content-between pe-3">
                              <div
                                onClick={() =>
                                  row.status === "DEACTIVATED" &&
                                  row?.isTotalDraft == false
                                    ? null
                                    : handlePreviewPage(row)
                                }
                                className={`${styles.propertyNameHover} ${
                                  cursorLoading ? styles.cursorLoading : ""
                                }`}
                              >
                                {row?.propertyName
                                  ? row?.propertyName?.length > 10
                                    ? row?.propertyName.slice(0, 10) + "..."
                                    : row?.propertyName
                                  : "-"}
                              </div>
                              {console.log(row?.isTotalDraft)}
                              {row?.isTotalDraft === true ? (
                                <span className={styles.draftStyle}>Draft</span>
                              ) : (
                                ""
                              )}
                            </div>
                          </td>
                          <td>
                            {row.propertyTypeName ? row.propertyTypeName : "-"}
                          </td>
                          <td>
                            <div className={styles.statusBox}>
                              {row.status === "ACTIVE" ? (
                                <span className={styles.statusTextActive}>
                                  Active
                                </span>
                              ) : row.status === "DEACTIVATED" ? (
                                <span className={styles.statusTextInactive}>
                                  Inactive
                                </span>
                              ) : row.status === "UPCOMING" ? (
                                <span className={styles.statusTextUpcoming}>
                                  Upcoming
                                </span>
                              ) : (
                                "-"
                              )}
                            </div>
                          </td>
                          <td>
                            {/* Need to show Approved At Date */}
                            {"-"}
                          </td>
                          <td>{row?.city ? row.city : "-"}</td>
                          <td>{row?.area ? row.area : "-"}</td>
                          {console.log(row)}
                          <td>
                            {row?.mobileNumber
                              ? `+91-${row?.mobileNumber}`
                              : "-"}
                          </td>
                          <td>
                            {row?.pocDetails?.name ? (
                              <div className="d-flex align-items-center justify-content-between">
                                <p className="mb-0 w-100">
                                  {row?.pocDetails?.name}
                                </p>
                                <LightTooltip
                                  title={
                                    <div className="px-2 py-3">
                                      <div className={styles.detailsLabel}>
                                        {row?.pocDetails?.name}
                                      </div>
                                      <div className="d-flex col-12 mt-1 ">
                                        <div className="d-block col-6 ">
                                          <p className={styles.rolelabel}>
                                            {" "}
                                            role
                                          </p>
                                          <p
                                            className={styles.roleDetailsvalue}
                                          >
                                            {row?.pocDetails?.role
                                              .replace(/_/g, " ")
                                              .toLowerCase()}
                                          </p>
                                        </div>
                                        <div className="d-block col-6 mx-2">
                                          <p className={styles.rolelabel}>
                                            {" "}
                                            Contact
                                          </p>
                                          <p
                                            className={styles.roleDetailsvalue}
                                          >
                                            {`${row?.pocDetails?.countryCode}-${row?.pocDetails?.mobileNumber}`}
                                          </p>
                                        </div>
                                      </div>
                                    </div>
                                  }
                                  arrow
                                  placement="top-end"
                                  style={{ backgroundColor: "white" }}
                                >
                                  <img
                                    src={info}
                                    className={`${styles.infoIcon}`}
                                    alt=""
                                  />
                                </LightTooltip>
                              </div>
                            ) : (
                              "-"
                            )}
                          </td>
                          <td>
                            {row?.secPocDetails &&
                            row?.secPocDetails[0].name == "" &&
                            row?.secPocDetails[1].name == "" ? (
                              "-"
                            ) : row?.secPocDetails &&
                              row?.secPocDetails[1]?.name != "" ? (
                              <div className={` ${styles.secPocvalue}`}>
                                <p className="mb-0">
                                  {row?.secPocDetails &&
                                    row?.secPocDetails
                                      .map((poc, index, array) => {
                                        if (
                                          poc?.name?.length > 5 &&
                                          index == 1
                                        ) {
                                          return poc.name.slice(0, 5) + "... ";
                                        } else if (
                                          poc?.name?.length > 5 &&
                                          index == 0
                                        ) {
                                          return poc.name.slice(0, 5) + "...";
                                        } else {
                                          return poc.name;
                                        }
                                      })
                                      .join(", ")}
                                </p>
                                <LightTooltip
                                  title={
                                    <div className="px-2 py-3">
                                      {row?.secPocDetails.map((poc, index) => (
                                        <div key={poc._id}>
                                          <div className={styles.detailsLabel}>
                                            {poc?.name}
                                          </div>
                                          <div className="d-flex col-12 mt-1">
                                            <div className="d-block col-6 pe-1">
                                              <p className={styles.rolelabel}>
                                                {" "}
                                                role
                                              </p>
                                              <p
                                                className={
                                                  styles.roleDetailsvalue
                                                }
                                              >
                                                {poc?.role
                                                  .replace(/_/g, " ")
                                                  .toLowerCase()}
                                              </p>
                                            </div>
                                            <div className="d-block col-6  ">
                                              <p className={styles.rolelabel}>
                                                {" "}
                                                Contact
                                              </p>
                                              <p
                                                className={
                                                  styles.roleDetailsvalue
                                                }
                                              >
                                                {`${poc?.countryCode}-${poc?.mobileNumber}`}
                                              </p>
                                            </div>
                                          </div>
                                          {index == 0 && <hr />}
                                        </div>
                                      ))}
                                    </div>
                                  }
                                  arrow
                                  placement="top-end"
                                  style={{ backgroundColor: "white" }}
                                >
                                  {" "}
                                  <img
                                    src={info}
                                    className={`${styles.infoIcon}`}
                                    alt=""
                                  />
                                </LightTooltip>
                              </div>
                            ) : (
                              <div className={` ${styles.secPocvalue}`}>
                                <p className="mb-0">
                                  {row?.secPocDetails.map((poc) => poc.name)}
                                </p>
                                <LightTooltip
                                  title={
                                    <div className="px-2 py-3">
                                      {row?.secPocDetails?.length > 0 && (
                                        <div key={row?.secPocDetails[0]._id}>
                                          <div className={styles.detailsLabel}>
                                            {row?.secPocDetails[0].name}
                                          </div>
                                          <div className="d-flex col-12 mt-1">
                                            <div className="d-block col-6 pe-1">
                                              <p className={styles.rolelabel}>
                                                Role
                                              </p>
                                              <p
                                                className={
                                                  styles.roleDetailsvalue
                                                }
                                              >
                                                {row.secPocDetails[0].role
                                                  .replace(/_/g, " ")
                                                  .toLowerCase()}{" "}
                                              </p>
                                            </div>
                                            <div className="d-block col-6 ">
                                              <p className={styles.rolelabel}>
                                                Contact
                                              </p>
                                              <p
                                                className={
                                                  styles.roleDetailsvalue
                                                }
                                              >
                                                {`${row.secPocDetails[0].countryCode}-${row.secPocDetails[0].mobileNumber}`}
                                              </p>
                                            </div>
                                          </div>
                                        </div>
                                      )}
                                    </div>
                                  }
                                  arrow
                                  placement="top-end"
                                  style={{ backgroundColor: "white" }}
                                >
                                  <img
                                    src={info}
                                    className={`${styles.infoIcon}`}
                                  />
                                </LightTooltip>
                              </div>
                            )}
                          </td>

                          <td>{row.totalUnits ? row.totalUnits : "-"}</td>
                          <td className={styles.lastColumnLeftBorder}>
                            <button
                              disabled={
                                row.approvalStatus == "PENDING" &&
                                row?.isTotalDraft == false
                                  ? true
                                  : false
                              }
                              className={
                                activeFrameIndex == index
                                  ? styles.removeButtonActive
                                  : styles.removeButton
                              }
                              onClick={(e) => handleOpenFrame(row, index)}
                            >
                              &#8942;
                            </button>
                          </td>
                        </tr>
                      ))
                    ) : loader ? (
                      <td colSpan={10}>
                        <PageLoader />
                      </td>
                    ) : (
                      <tr>
                        <td colSpan={10} className=" text-center pe-5">
                          <div className={styles.noPropertyList}>
                            <img src={PropertyLogo} alt="" />
                            <span className={styles.noPropertylabel}>
                              {/* No Property added yet */}
                              Property Not Found
                            </span>
                            {/* <span className={styles.noPropertylabelDesc}>
                Get started by adding the first property in your Property List.
                Click the button below to proceed adding.
              </span>
              <NormalButton
                addBtn={true}
                onClick={() => handleAddProperty()}
                className={"addNewBtn"}
                label="Add Property"
              /> */}
                          </div>
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
              <div>
                {isFrameOpen &&
                  activeFrameIndex != null &&
                  activeFrameIndex == indexValue && (
                    <div
                      ref={statusDropdownRef}
                      className={styles.rowOptionsTopFrame}
                      style={{ top: 80 + indexValue * 46 }}
                    >
                      <div
                        className={` ${
                          indexData?.approvalStatus == "APPROVED"
                            ? styles.option
                            : styles.optionDisabled
                        }`}
                        onClick={() => {}}
                      >
                        <BsPlusCircle fontSize={16} />
                        {/* <img src={AddBtnLogo} alt="icon" /> */}
                        <span className="ps-2">Add Unit</span>
                      </div>
                      <div
                        className={` ${
                          indexData?.approvalStatus == "REJECTED"
                            ? styles.option
                            : styles.optionDisabled
                        }`}
                        onClick={() => {
                          if (indexData?.approvalStatus === "REJECTED") {
                            setReasonModal(true);
                            setStatusReason(indexData?.statusReason)
                          }
                        }}
                      >
                        <img
                          src={
                            indexData?.approvalStatus == "REJECTED"
                              ? Comment
                              : CommentLight
                          }
                        />

                        <span className="ps-2">View Reason</span>
                      </div>
                      <div
                        className={` ${
                          indexData?.isTotalDraft == true ||
                          indexData?.approvalStatus == "REJECTED"
                            ? styles.optionDelete
                            : styles.optionDisabled
                        }`}
                        onClick={() =>
                          indexData?.isTotalDraft == true ||
                          indexData?.approvalStatus == "REJECTED"
                            ? handleDeleteFunction(indexData?._id)
                            : undefined
                        }
                      >
                        <BsTrash fontSize={18} />
                        <span className="ps-2">Delete</span>
                      </div>
                      <div
                        className={` ${
                          (indexData?.approvalStatus == "APPROVED" &&
                            indexData?.status === "ACTIVE") ||
                          indexData?.status === "DEACTIVATED"
                            ? styles.option
                            : styles.optionDisabled
                        }`}
                        onClick={() =>
                          indexData?.approvalStatus == "APPROVED" &&
                          indexData?.status === "ACTIVE"
                            ? handleDeActivateAccount(indexData?._id)
                            : indexData?.status === "DEACTIVATED"
                            ? handleReActivateAccount(indexData?._id)
                            : alert("fsdf")
                        }
                      >
                        {indexData?.status === "DEACTIVATED" ? (
                          <>
                            <BsArrowCounterclockwise fontSize={16} />
                            <span className="ps-2">Reactivate Property</span>
                          </>
                        ) : (
                          <>
                            <BsDashCircle fontSize={16} />
                            <span className="ps-2">Deactivate Property</span>
                          </>
                        )}
                      </div>
                    </div>
                  )}
              </div>
            </div>
          </>
        ) : empty === true ? (
          <div>
            <div className={styles.noPropertyList}>
              <img src={PropertyLogo} />
              <span className={styles.noPropertylabel}>
                No Property added yet
              </span>
              <span className={styles.noPropertylabelDesc}>
                Get started by adding the first property in your Property List.
                Click the button below to proceed adding.
              </span>
              <NormalButton
                addBtn={true}
                onClick={() => handleAddProperty()}
                className={"addNewBtn"}
                label="Add Property"
              />
            </div>
          </div>
        ) : undefined}

        {/* {tableData.map((index) => (
          <div key={index}>
          </div>
        ))} */}
        {tableData?.length > 0 && empty == false && (
          <Pagination
            currentPage={currentPage}
            totalPages={totalPages}
            onPageChange={handlePageChange}
          />
        )}
      </div>
      <DeleteModal
        futureDescription="Details filled will no longer be saved."
        showFutureDesc={true}
        visible={deleteModal}
        handleClose={() => setDeleteModal(false)}
        handleDelete={() => handleDelete()}
        description={`Are you sure you want to permanently delete this property? `}
        label="Delete"
      />
      <DeleteModal
        deleteModal={false}
        ReasonDescription={statusReason}
        showFutureDesc={true}
        visible={reasonModal}
        handleClose={() => setReasonModal(false)}
      />
    </>
  );
};

export default PropertyListComp;
